/* eslint-disable no-useless-escape */
class RegInput {
    constructor(
        value,
        title,
        type,
        id,
        placeholder,
        isValid,
        invalidFeedback,
        pattern
    ) {
        this.value = value;
        this.title = title;
        this.type = type;
        (this.class = {
            "form-control": true,
            "is-invalid": false,
            "is-valid": false
        }),
            (this.id = id);
        this.placeholder = placeholder;
        this.isValid = isValid;
        this.invalidFeedback = invalidFeedback;
        this.pattern = new RegExp(pattern);
    }
    validate() {
        const test = this.pattern.test(this.value);
        if (test) {
            this.isValid = true;
            this.class["is-valid"] = true;
            this.class["is-invalid"] = false;
        } else if (!test) {
            this.isValid = false;
            this.class["is-valid"] = false;
            this.class["is-invalid"] = true;
        }
    }
}

export default {
    firstName: new RegInput(
        null,
        "",
        "text",
        "first-name",
        " ",
        false,
        "      ",
        /^[--]{2,100}/
    ),
    lastName: new RegInput(
        null,
        "",
        "text",
        "last-name",
        " ",
        false,
        "      ",
        /^[--]{2,100}/
    ),
    email: new RegInput(
        null,
        "  ",
        "email",
        "reg-email",
        "example@mail.com",
        false,
        "    ",
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    ),
    password: new RegInput(
        null,
        "",
        "password",
        "reg-password",
        " ",
        false,
        "    5      ",
        /[0-9a-zA-Z]{6,30}/
    )
};
